.container {
  text-align: center;
  margin-right: 0%;
  justify-items: space-between;
}

h1,
h2,
h3 {
  text-align: center;
}

h1 {
  font-family: "PatuaOne-Regular";
}

.quotes {
  font-family: "PatuaOne-Regular";
  background-image: url("../../assets/usa.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: white;
  font-size: small;
  padding: 20px;
  border-radius: 5px;
}

.img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 10px;
}

.titleFlex {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  padding: 10px;
  max-width: 1000px;
  justify-content: space-between;
}

.graffitiText {
  color: rgb(28, 40, 207);
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  font-family: "UrbanHeroes";
}

.quotemark {
  font-size: large;
}

@media screen and (max-width: 560px) {
  .img {
    display: none;
  }

  h1 {
    padding-left: 20px;
    font-size: x-large;
  }
}

@media screen and (max-width: 410px) {
  h1 {
    padding-left: 20px;
    font-size: large;
  }
}
